<template>
<div class="jumbotron">
 <div class="container">
	<h1>OA</h1>
	<p class="text-justify">wiki</p>
	<div class="form-inline">
		<div class="form-group">
			<label class="sr-only"></label>
			<input type="text" class="form-control" v-model="user.username" placeholder="">
		</div>
		<div class="form-group">
			<label class="sr-only"></label>
			<input type="password" class="form-control" v-model="user.password"  placeholder="">
		</div>
		<button type="button" class="btn btn-primary" @click="loginHandler"></button>
	</div>
	</div>
</div>
</template>
<script>
	import {mapGetters, mapActions} from "vuex";
	export default{
		data(){
			return {
				user:{
					username:"",
					password:""
				}
			}
		},
		computed:{
			...mapGetters({
				loginStatus:"getLoginStatus"
			}),
		},
		created(){
			if(this.loginStatus){
				let refers = decodeURIComponent(this.$route.query.refer);
				let pathName = null;
				let pathParams = {};
				if(refers.indexOf("?")>0){
					pathName = refers.split("?")[0];
					let temp = refers.split("?")[1].split("&");
					if(temp !== null && temp.length !==0){
						for(let item of temp){
							item = item.split("=");
							Object.defineProperty(pathParams,item[0],{value:item[1]})
						}
					}
				}
				if(this.$route.query.hasOwnProperty("refer")){
					if(this.$route.query.refer !== '' && pathName !== null){
						this.$router.push({
							name:pathName,
							query:pathParams
						})
					}else{
						this.$router.push({
							name:"main"
						})
					}
				}else{
					this.$router.push({
						name:"main"
					})
				}
			}
		},
		methods:{
			loginHandler(){
				if(this.user.username.length === 0){
					return false;
				}
				if(this.user.password.length ===0){
					return false;
				}
				this.$store.dispatch("loginAction",this.user)
				.then(()=>{
					if(this.loginStatus){
						let refers = decodeURIComponent(this.$route.query.refer);
						let pathName = null;
						let pathParams = {};
						if(refers.indexOf("?")>0){
							pathName = refers.split("?")[0];
							let temp = refers.split("?")[1].split("&");
							if(temp !== null && temp.length !==0){
								for(let item of temp){
									item = item.split("=");
									Object.defineProperty(pathParams,item[0],{value:item[1]})
								}
							}
						}
						if(this.$route.query.hasOwnProperty("refer")){
							if(this.$route.query.refer !== '' && pathName !== null){
								this.$router.push({
									name:pathName,
									query:pathParams
								})
							}else{
								this.$router.push({
									name:"main"
								})
							}
						}
					}else{
						console.log("login failed")
					}
				})
				.catch((error)=>{
					console.log(error)
				});
			}
		}
	}
</script>